.gpt3__footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: var(--color-footer);
}
.gpt3__footer-heading{
    margin-bottom: 3rem;
    text-align: center;
    width:100%;
}
.gpt3__footer-heading h1{
        font-family: var(--font-family);
        font-size: 62px;
        font-weight: 800;
        line-height: 75px;
        letter-spacing: -0.04em;
}
.gpt3__footer-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1.4px solid #fff;
    padding: 1rem;
    margin-bottom: 10rem;
    text-align: center;
    cursor: pointer;
}
.gpt3__footer-btn p{
    color: #FFF;
        font-family:var(--font-family);
        font-size: 18px;
        font-weight: 400;
        line-height: 21px;
        word-spacing: 2px;
}
.gpt3__footer-links{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    width:100%;
    text-align: left;
}
.gpt3__footer-links div{
    margin: 1rem;
    width: 250px;
}
.gpt3__footer-links_logo{
    display: flex;
    flex-direction: column;
}

.gpt3__footer-links_logo img{
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;
}

.gpt3__footer-links_logo p{
    color: #FFF;
        font-family: var(--font-family);
        font-size: 12px;
        font-weight: 400;
        line-height: 15px;
}

.gpt3__footer-links_div{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.gpt3__footer-links_div h4{
    color: #FFF;
        font-family: var(--font-family);
        font-size: 14px;
        font-weight: bolder;
        line-height: 17px;
        margin-bottom: 0.9rem;
}

.gpt3__footer-links_div p{
    color: #FFF;
        font-family: var(--font-family);
        font-size: 12px;
        line-height: 15px;
        margin: 0.5rem 0;
}

.gpt3__footer-copyright{
    width: 100%;
    text-align: center;
    margin-top: 2rem;
}
.gpt3__footer-copyright p{
    font-size: 12px;
     font-family: var(--font-family);
     line-height: 15px;
     color: #fff;
}

@media screen and (max-width:850px) {

.gpt3__footer-heading h1 {
       
        font-size: 44px;
        line-height: 50px;       
    }
}

@media screen and (max-width:550px) {
    .gpt3__footer-heading h1 {
            font-size: 34px;
            line-height: 42px;
        }
    
        .gpt3__footer-links div {
            margin: 1rem ;
        }
    
        .gpt3__footer-btn p {
            font-size: 14px;
            line-height: 20px;
        }
}

@media screen and (max-width: 400px) {
    .gpt3__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}
